<script>
import debug from 'debug';
const log = debug('app:components/PDFPage');

import {PIXEL_RATIO} from '../utils/constants';

export default {
  props: {
    page: {
      type: Object, // instance of PDFPageProxy returned from pdf.getPage
      required: true,
    },
    scale: {
      type: Number,
      required: true,
    },
    scrollBounds: {
      type: Object,
      default: () => ({}),
    },
    elementBounds: {
      type: Object,
      default: () => ({}),
    },
    isPageFocused: {
      type: Boolean,
      default: false,
    },
    isElementVisible: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    actualSizeViewport() {
      return this.viewport.clone({scale: this.scale});
    },

    canvasStyle() {
      const {width: actualSizeWidth, height: actualSizeHeight} = this.actualSizeViewport;
      const [pixelWidth, pixelHeight] = [actualSizeWidth, actualSizeHeight]
        .map(dim => Math.ceil(dim / PIXEL_RATIO));
      return `width: ${pixelWidth}px; height: ${pixelHeight}px;`;
    },

    canvasAttrs() {
      let {width, height} = this.viewport;
      [width, height] = [width, height].map(dim => Math.ceil(dim));
      const style = this.canvasStyle;

      return {
        width,
        height,
        style,
        class: 'pdf-page box-shadow',
      };
    },

    pageNumber() {
      return this.page.pageNumber;
    },

    isElementFocused() {
      const {top: scrollTop} = this.scrollBounds;
      const {top, bottom, height} = this.elementBounds;
      const halfHeight = (height / 2);

      return height > 0 &&
        (top - halfHeight) <= scrollTop &&
        scrollTop < (bottom - halfHeight);
    },
  },

  methods: {
    focusElement() {
      if (this.isElementFocused) return;

      const {top} = this.elementBounds;
      this.$emit('page-top', top);
    },

    focusPage() {
      if (this.isPageFocused) return;

      this.$emit('page-focus', this.pageNumber);
    },

    drawPage() {
      if (this.renderTask) return;

      const {viewport} = this;
      const canvasContext = this.$el.getContext('2d');
      const renderContext = {canvasContext, viewport};

      // PDFPageProxy#render
      // https://mozilla.github.io/pdf.js/api/draft/PDFPageProxy.html
      this.renderTask = this.page.render(renderContext);
      this.renderTask
        .then(() => {
          this.$emit('page-rendered', {
            page: this.page,
            text: `Rendered page ${this.pageNumber}`,
          });
         })
        .catch(response => {
          this.destroyRenderTask();
          this.$emit('page-errored', {
            response,
            page: this.page,
            text: `Failed to render page ${this.pageNumber}`,
          });
        });
    },

    updateVisibility() {
      this.$parent.$emit('update-visibility');
    },

    destroyPage(_newPage, page) {
      // PDFPageProxy#_destroy
      // https://mozilla.github.io/pdf.js/api/draft/PDFPageProxy.html
      if (page) page._destroy();

      this.destroyRenderTask();
    },

    destroyRenderTask() {
      if (!this.renderTask) return;

      // RenderTask#cancel
      // https://mozilla.github.io/pdf.js/api/draft/RenderTask.html
      this.renderTask.cancel();
      delete this.renderTask;
    },
  },

  watch: {
    page: 'destroyPage',
    scale: 'updateVisibility',

    isPageFocused(isPageFocused) {
      if (isPageFocused) this.focusElement();
    },

    isElementFocused(isElementFocused) {
      if (isElementFocused) this.focusPage();
    },

    isElementVisible(isElementVisible) {
      if (isElementVisible) this.drawPage();
    },
  },

  created() {
    // PDFPageProxy#getViewport
    // https://mozilla.github.io/pdf.js/api/draft/PDFPageProxy.html
    this.viewport = this.page.getViewport(this.scale);
  },

  mounted() {
    log(`Page ${this.pageNumber} mounted`);
    this.updateVisibility();
    if (this.isElementVisible) this.drawPage();
    if (this.isElementFocused) this.focusPage();
  },

  beforeDestroy() {
    this.destroyPage(undefined, this.page);
  },

  render(h) {
    const {canvasAttrs: attrs} = this;
    return h('canvas', {attrs});
  },
};
</script>
<style>
.pdf-page {
  display: block;
  margin: 0 auto 1em;
}
</style>
